@media only screen and (max-width: 600px) {
  .map .map-select {
    display: none;
  }

  .info-icon {
    display: none;
  }

  .leaflet-control-zoom {
    display: none;
  }

  .elevation-chart {
    display: none;
  }
}

.x-axis {
  font-size: 12px;
