  opacity: 0.5;
}

.y-axis {
  font-size: 12px;
  color: #ccc;
  opacity: 0.5;
}

.elevation-chart {
  position: fixed !important;
  bottom: 0 !important;
  margin: 10px;
  -webkit-transform: none !important;
          transform: none !important;
  top: auto !important;
  top: initial !important;
  border-radius: 5px;
  opacity: 0.8;
  cursor: -webkit-grab;
  cursor: grab;
}

@media only screen and (max-width: 600px) {
  .elevation-chart {
    display: none !important;
  }
}

